












import { Component, Vue } from 'vue-property-decorator';

@Component
export default class HelloWorld extends Vue {
  public name: string = "Hello Kathi - I am your project";
  public number: number = 0;

  public beforeMount(){
    this.countUp();
  }

  public countUp(){
    this.number++;
    setTimeout(() => {this.countUp()}, 1000);
  }
}
